#App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 1.5em;
  background: -webkit-linear-gradient(27deg, teal 23%, transparent 23%) 7px 0,
    -webkit-linear-gradient(27deg, transparent 74%, teal 78%),
    -webkit-linear-gradient(
      27deg,
      transparent 34%,
      teal 38%,
      teal 58%,
      transparent 62%
    ),
    #333;
  background: -o-linear-gradient(27deg, teal 23%, transparent 23%) 7px 0,
    -o-linear-gradient(27deg, transparent 74%, teal 78%),
    -o-linear-gradient(
      27deg,
      transparent 34%,
      teal 38%,
      teal 58%,
      transparent 62%
    ),
    #333;
  background: linear-gradient(63deg, teal 23%, transparent 23%) 7px 0,
    linear-gradient(63deg, transparent 74%, teal 78%),
    linear-gradient(
      63deg,
      transparent 34%,
      teal 38%,
      teal 58%,
      transparent 62%
    ),
    #333;
  background-size: 16px 48px;
  /*border: 1px solid salmon;*/
}
#App div#FirstDiv {
  display: block;
  /* box-shadow: 0 2px 15px rgba(0, 0, 0, .4); */
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  background-color: ghostwhite;
  min-width: 350px;
  width: 90vw;
  min-height: 200px;
  height: 100%;
  border-radius: 5px;
}



